	data() {
		return {
			scrollH: 500,
			news_list: [],
			loadtext: '上拉加载更多'
		};
	},
	onLoad() {
		// 获取可视区域高度
		uni.getSystemInfo({
			success: res => {
				this.scrollH = res.windowHeight;
			}
		});
		// 初始化事件
		this.__init();
	},
	onNavigationBarSearchInputClicked() {
		uni.navigateTo({
			url: '../search/search'
		});
	},
	methods: {
		// 初始化事件
		__init() {
			this.news_list = demo1;
		},

		// 上拉加载更多
		loadmore() {
			let news = this.news_list;
			// 是否处于可加载状态
			if (this.loadtext !== '上拉加载更多') return;
			// 模拟加载
			this.loadtext = '加载中...';
			setTimeout(() => {
				// 加载数据
				this.news_list = [...news, ...demo2];
				// 恢复状态
				this.loadtext = '上拉加载更多';
			}, 2000);
		}
	}
